import { useEffect } from "react";
import { useGlobalStore } from "@/lib/store";

export const colors = {
  teal: {
    primary: "#457E7F",
    light: "rgba(69, 126, 127, 0.1)",
    dark: "rgba(59, 108, 109, 0.9)",
  },
  orange: {
    primary: "#E36A20",
    light: "rgba(227, 106, 32, 0.1)",
    dark: "rgba(193, 90, 27, 0.9)",
  },
};

export const useThemeToggler = () => {
  const { theme, setTheme } = useGlobalStore();

  const toggleTheme = () => {
    const newTheme = theme === "teal" ? "orange" : "teal";
    setTheme(newTheme);
  };

  useEffect(() => {
    document.documentElement.style.setProperty(
      "--primary",
      colors[theme].primary
    );
    document.documentElement.style.setProperty(
      "--primary-light",
      colors[theme].light
    );
    document.documentElement.style.setProperty(
      "--primary-dark",
      colors[theme].dark
    );
  }, [theme]);

  return {
    theme,
    toggleTheme,
  };
};
