"use client";
import Image from "next/image";
import React, { useEffect, useRef, useState } from "react";
import { benefits } from "./EverdayBenefits.constants";
import { useGlobalStore } from "@/lib/store";
import { mediaLoader } from "@/lib/media-loader";
import Link from "next/link";

function EverdayBenefits() {
  const [isVisible, setIsVisible] = useState(false);
  const [currentIndex, setCurrentIndex] = useState(0);
  const sectionRef = useRef<HTMLDivElement>(null);
  const carouselRef = useRef<HTMLDivElement>(null);

  const { theme } = useGlobalStore();
  const isTeal = theme === "teal";

  // animation on scrollf
  useEffect(() => {
    const currentRef = sectionRef.current;
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      {
        threshold: 0.2,
        rootMargin: "0px 0px -50px 0px",
      }
    );

    if (currentRef) {
      observer.observe(currentRef);
    }

    return () => {
      if (currentRef) {
        observer.unobserve(currentRef);
      }
    };
  }, []);

  // Auto-carousel effect
  useEffect(() => {
    if (!isVisible) return;
    const interval = setInterval(() => {
      setCurrentIndex((prevIndex) => {
        const nextIndex = prevIndex + 1;
        return nextIndex >= benefits[theme].length ? 0 : nextIndex;
      });
    }, 4000);
    return () => clearInterval(interval);
  }, [isVisible, theme]);

  // Smooth scroll to current index image
  useEffect(() => {
    if (carouselRef.current) {
      const scrollWidth = carouselRef.current.scrollWidth;
      const totalItems = benefits[theme].length;
      const itemWidth = scrollWidth / totalItems;

      carouselRef.current.scrollTo({
        left: itemWidth * currentIndex,
        behavior: "smooth",
      });
    }
  }, [currentIndex, theme]);

  return (
    <>
      <div
        ref={sectionRef}
        className=" w-full px-6 pr-0 sm:px-16 sm:pr-0 py-7 flex flex-col gap-4  sm:flex-row justify-between items-center "
      >
        {/* left  */}
        <div
          className={`transition-all flex items-center duration-1000 ease-out pr-6 sm:max-w-1/2
             ${
               isVisible
                 ? "opacity-100 sm:translate-x-0"
                 : "opacity-0 sm:-translate-x-20"
             }
          `}
        >
          {/* <h2 className="hidden sm:block text-2xl sm:text-4xl font-semibold ">
            EVERYDAY
            <span className="italic text-primary"> BENEFITS</span>
          </h2> */}

          <Image
            src={mediaLoader(
              isTeal
                ? "/GP-oil.gif"
                : "/WW-oil.gif"
            )}
            alt="everyday benefits animation"
            className="size-22 sm:size-40"
            width={160}
            height={160}
            sizes="(max-width: 640px) 88px, 136px"
            unoptimized
          />

          <h6 className="text-base text-black sm:text-2xl sm:line-clamp-3 text-left">
            {isTeal
              ? `GutPro doesn’t change the dish, it changes the aftertaste of your day.`
              : `
Weight Watchers doesn’t change the dish, it makes your everyday meals lighter.
`}
          </h6>
        </div>

        {/* right */}
        <div
          ref={carouselRef}
          className={`flex rounded-3xl sm:rounded-l-3xl sm:gap-0 transition-all sm:max-w-1/2 overflow-x-auto scroll-smooth duration-1000 ease-out scrollbar-hide 
            ${
              isVisible
                ? "opacity-100 translate-x-0"
                : "opacity-0 translate-x-full sm:translate-x-20"
            }
          `}
        >
          {benefits[theme].map((benefit, index) => {
            // Check if this is the active item
            const isActive = index === currentIndex;
            return (
              <div
                key={benefit.id}
                className={`relative mr-5 flex-shrink-0 transition-opacity duration-700 ${
                  isActive ? "opacity-100" : "opacity-90"
                }`}
              >
                <Image
                  src={benefit.image}
                  alt={`images ${benefit.image}`}
                  width={300}
                  height={300}
                  className="rounded-3xl w-3xs md:max-w-none md:w-75 "
                  sizes="(max-width: 640px) 250px, 300px"
                />

                <h3 className="text-base text-white absolute bottom-0 z-11 p-4">
                  {/* {index} */}
                  {benefit.title}
                </h3>

                <div
                  className="absolute inset-0 z-10 rounded-3xl"
                  style={{
                    background:
                      "linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(177, 177, 177, 0.2) 49.04%, rgba(0, 0, 0, 0.8) 87.5%, #000000 100%)",
                  }}
                ></div>
              </div>
            );
          })}
        </div>
      </div>

      <div className="bg-primary text-white py-6 sm:py-7 px-6 sm:px-16   mx-auto flex flex-col sm:flex-row items-center justify-between gap-8">
        <div
          className={`flex-1 transition-all duration-1000 ease-out
          ${
            isVisible
              ? "opacity-100 sm:translate-x-0"
              : "opacity-0 sm:-translate-x-20"
          }
          `}
        >
          <h2 className=" font-semibold text-2xl sm:text-4xl mb-1 md:mb-2">
            {isTeal ? "MEALS MADE PRO-DIGEST" : "MEALS THAT FEEL LIGHT"}
          </h2>
          <p className="text-sm sm:text-lg font-light">
            {isTeal
              ? "Your favourite dishes now the Gut Pro way."
              : "Your favourites, the Weight Watchers way."}
          </p>
        </div>

        <Link
          href={"/blogs"}
          className={`hidden sm:block bg-white text-primary px-6 py-2 rounded-md  hover:scale-101 whitespace-nowrap
            transition-all duration-1000 ease-out
          ${
            isVisible ? "opacity-100 translate-x-0" : "opacity-0 translate-x-20"
          }
          `}
        >
          {isTeal ? "Try a Gut Pro Recipe" : "Try a Weight Watchers Recipe"}
        </Link>
      </div>
    </>
  );
}

export default EverdayBenefits;
