"use client";
import Image from "next/image";
import { useEffect, useRef, useState } from "react";
import { useGlobalStore } from "@/lib/store";
import { gutProSectionConfig } from "./gutProSectionConfig";

export default function GutProSection() {
  const [isVisible, setIsVisible] = useState(false);
  const sectionRef = useRef<HTMLDivElement>(null);

  const { theme } = useGlobalStore();
  const content = gutProSectionConfig[theme];

  useEffect(() => {
    const currentRef = sectionRef.current;
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      {
        threshold: 0.2, // Trigger when 20% of the component is visible
        rootMargin: "0px 0px -50px 0px",
      }
    );

    if (currentRef) {
      observer.observe(currentRef);
    }

    return () => {
      if (currentRef) {
        observer.unobserve(currentRef);
      }
    };
  }, []);

  return (
    <>
    
    
    <div
      ref={sectionRef}
      className="py-8 sm:py-15   px-6 sm:px-16 container mx-auto w-full flex flex-col-reverse sm:flex-row gap-5 lg:gap-24 items-center "
    >
      {/* <div className="absolute left-1/2 top-0 h-full w-[1px] bg-black"></div> */}

      {/* Left Side - Features */}
      <div
        className={`space-y-3 transition-all duration-1000 ease-in-out w-full sm:w-1/2
            ${
              isVisible
                ? "opacity-100 translate-x-0"
                : "opacity-0 -translate-x-full"
            }
          `}
      >
        {content.features.map((feature, index) => (
          <div
            key={index}
            className="flex items-center justify-between rounded-full border-2 border-primary px-5 py-1 w-full max-w-md"
          >
            <span className="text-foreground flex-1 text-sm sm:text-xl font-medium">
              {feature.text}
            </span>

            <Image
              className="size-12 sm:size-15"
              src={feature.src}
              width={60}
              height={60}
              alt={feature.alt}
              quality={100}
            />
          </div>
        ))}
      </div>

      {/* Right Side - Heading */}
      <div
        className={`w-full sm:w-1/2  transition-all duration-1000 ease-in-out 
            ${
              isVisible
                ? "opacity-100 translate-x-0"
                : "opacity-0 translate-x-full"
            }
          `}
      >
        <div className="px-6 sm:p-0">
          <p className="text-foreground text-xl sm:text-2xl ">
            {content.heading.topText}
          </p>

          <h2
            className={`${content.heading.firstLine.h2ClassName} ${content.heading.firstLine.className}`}
          >
            {content.heading.firstLine.text}
            {content.heading.firstLine.span1 && (
              <span className={content.heading.firstLine.span1.className}>
                {content.heading.firstLine.span1.text}
              </span>
            )}
          </h2>

          <h2 className={`text-end md:text-left mb-2 ${content.heading.secondLine.h2ClassName}`}>
            {content.heading.secondLine.span1 && (
              <>
                <span className={content.heading.secondLine.span1.className}>
                  {content.heading.secondLine.span1.text}
                </span>{" "}
              </>
            )}
            <span className={content.heading.secondLine.span2.className}>
              {content.heading.secondLine.span2.text}
            </span>{" "}
            {content.heading.secondLine.span3 && (
              <span
                className={` ${content.heading.secondLine.span3.className}`}
              >
                {content.heading.secondLine.span3.text}
              </span>
            )}
          </h2>
        </div>

        <p className="text-black font-light text-sm sm:text-xl text-center sm:text-left">
          {content.description}
        </p>
      </div>
    </div>

      {theme === "teal" ? (
        <p className="font-light text-[7px] sm:text-[10px] mx-auto  px-6 sm:px-16 text-black container">
          ^#The claim and benefits of this product are based on scientific research and findings published in publicly available papers and literature. | Results may vary based on individual circumstances and usage. | Eating cooked food in this oil is not the only factor responsible for managing normal GUT functioning. Regular physical exercise, balancing your diet, managing your food intake, maintaining optimum weight and genetics, etc are several other factors for managing normal gut functioning.
        </p>
      ) : (
        <p className="font-light text-[7px] sm:text-[10px] mx-auto px-6 sm:px-16 text-black container">
          ^Based on frying study, 2024-2025. | #Due to low absorption of oil there is a net reduction in the fat absorption in the food that may have weight management effects. | Eating food cooked in low absorbing oil is not the only factor responsible for managing weight. Regular physical exercise, balancing your diet, managing your food intake, maintaining optimum weight and genetics etc., are several other factors for managing the weight.
        </p>
      )}
    </>
  );
}
