"use client";
import Image from "next/image";
import React, { useEffect, useState } from "react";
import { ChevronLeft, ChevronRight } from "lucide-react";
import { useThemeToggler } from "@/app/hooks/useThemeToggler";
import { mediaLoader } from "@/lib/media-loader";

function HeroSection() {
  const [visible, setIsVisible] = useState(false);
  const { theme, toggleTheme } = useThemeToggler();

  useEffect(() => {
    setIsVisible(false);
    const timer = setTimeout(() => {
      setIsVisible(true);
    }, 1500);
    return () => {
      clearInterval(timer);
    };
  }, [theme]);
  return (
    <div className="relative w-full min-h-screen">
      {/* Background Image with color Overlay */}
      <div className="absolute inset-0">
        <div
          className="absolute inset-0 bg-cover bg-center"
          style={{
            backgroundImage: `url(${mediaLoader(
              `/products-page/${theme}-theme-bg.v2.png`
            )})`,
          }}
        ></div>

        {/* Solid background layer */}
        <div
          className={`absolute inset-0 bg-primary transition-opacity duration-300 ease-in-out transform-gpu ${
            visible ? "opacity-0 " : "opacity-100"
          }`}
        />

        {/* Gradient background layer */}
        <div
          style={{
            background:
              "linear-gradient(179.98deg, var(--primary-light) 0.02%, var(--primary-dark) 87%, var(--primary) 99.98%)",
          }}
          className={`absolute inset-0 transition-opacity duration-2000  ease-in-out transform-gpu ${
            visible ? "opacity-100 " : "opacity-0 "
          }`}
        />
      </div>

      {/* Main Content */}
      <div
        className={`relative z-10 w-full pt-25 sm:pt-[140px] sm:pb-10 max-w-7xl mx-auto px-6 md:px-16 flex items-center justify-between gap-8
        `}
      >
        <button
          onClick={toggleTheme}
          className="text-white hover:scale-110 transition-transform  absolute left-0 "
          aria-label="Previous slide"
        >
          <ChevronLeft size={30} />
        </button>

        {/* Center Content */}
        <div
          className={`flex flex-col  sm:flex-row items-center justify-center gap-8 md:gap-40 flex-1 transition-all duration-700 ease-out
          `}
        >
          {/* Left Section - Text */}
          <div
            className={`flex flex-col items-center
                transition-transform duration-1000 ease-out will-change-transform transform-gpu
            ${
              visible
                ? "sm:items-start translate-x-0"
                : "translate-x-[calc(50vw-50%-64px)] "
            }
            `}
          >
            {/* Heading */}
            <Image
              width={300}
              height={260}
              src={
                theme === "teal"
                  ? "/products-page/uplife-gut-pro-logo.v2.png"
                  : "/products-page/weight-watchers/uplife-weight-watchers.v2.png"
              }
              alt="uplife gut pro logo"
              className={`w-40 sm:w-75  mb-2 sm:mb-8 transition-all duration-200 ease-out
                ${visible ? "" : "sm:scale-110"}`}
              sizes="(max-width: 640px) 200px,
                    320"
              priority
            />

            {theme === "teal" ? (
              <Image
                src="/products-page/indias-first-gut-pro-oil.png"
                width={404}
                height={120}
                alt="India's first gut pro oil"
                className={`w-[236px] sm:max-w-[404px] transition-all duration-200 ease-out
                  ${visible ? "" : "sm:scale-150"}
                  `}
                sizes="(max-width: 640px) 250px, 450px"
                priority
              />
            ) : (
              <div
                className={` text-white text-3xl transition-all duration-200 ease-out
                  ${visible ? "" : "sm:scale-150"}
                  `}
              >
                <h2 className="text-xl/5 sm:text-3xl font-medium uppercase italic mt-5">
                  YOUR
                  <span className="block font-bold">WEIGHT MANAGEMENT</span>
                  <span className="block text-end ">PARTNER</span>
                </h2>
              </div>
            )}
          </div>

          {/* Right Section - Product Image */}
          <div
            className={`relative  w-full sm:w-auto flex sm:block justify-center
                transition-transform duration-1000 ease-out will-change-transform transform-gpu
                ${visible ? "translate-x-0" : "translate-x-full ml-auto"}
            `}
          >
            <Image
              src={
                theme === "teal"
                  ? "/products-page/ProductsSection/teal-product-07.png"
                  : "/products-page/weight-watchers/orange-product-07.png"
              }
              width={380}
              height={540}
              alt={
                theme === "teal"
                  ? "gut pro oil packaging"
                  : "weight Watchers pouch packaging"
              }
              className="w-75 sm:w-80 object-contain "
              priority
              sizes="(max-width: 640px) 300px,
                    380"
            />
          </div>
        </div>

        {/* Right Arrow */}
        <button
          onClick={toggleTheme}
          className="text-white hover:scale-110 transition-transform  right-0 absolute"
          aria-label="Next slide"
        >
          <ChevronRight size={30} />
        </button>
      </div>
    </div>
  );
}

export default HeroSection;
