"use client";
import { useGlobalStore } from "@/lib/store";
import Image from "next/image";
import React, { useEffect, useRef, useState } from "react";
import { semiCircleConfig, commonAssets } from "./semiCircleConfig";
import { Search } from "lucide-react";

function SemiCircle() {
  const [isVisible, setIsVisible] = useState(false);
  const sectionRef = useRef<HTMLDivElement>(null);

  const { theme } = useGlobalStore();

  const currentTheme = theme === "teal" ? "teal" : "orange";
  const content = semiCircleConfig[currentTheme];

  useEffect(() => {
    const currentRef = sectionRef.current;
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      {
        threshold: 0.1, // Trigger when 10% of the component is visible
        rootMargin: "0px 0px -100px 0px", // Trigger slightly before it comes into view
      }
    );

    if (currentRef) {
      observer.observe(currentRef);
    }

    return () => {
      if (currentRef) {
        observer.unobserve(currentRef);
      }
    };
  }, []);

  return (
    <div ref={sectionRef} className="w-full aspect-[2/1] bg-primary -mt-1 sm:m-0">
      <div
        className={`relative h-full w-full rounded-t-full bg-primary mx-auto
            transition-all duration-1000 ease-in-out 
        ${isVisible ? "opacity-100" : "opacity-0 "}
        `}
      >
        <Image
          src={content.image.src}
          alt={content.image.alt}
          width={1280}
          height={640}
          sizes="100vw"
          className={`w-full h-full rounded-t-full z-1 object-cover ${content.image.objectPosition} pointer-events-none`}
        />

        {/* Hero Content Overlay */}
        <div className="absolute  inset-0 z-10 flex flex-col items-center justify-center mb-1 sm:justify-evenly py-8 px-6 sm:px-16 sm:py-0 text-white">
          <div className="max-w-xl w-full text-center hidden sm:block">
            {/* Main Heading */}

            <div className="bg-white/25 border-2 border-white backdrop-blur-xl rounded-full px-6 py-3 inline-flex items-center gap-3 mb-2 sm:mb-3">
              <Search className="w-5 h-5 sm:w-6 sm:h-6 text-white flex-shrink-0" />

              <div className="text-white text-sm sm:text-lg">
                {content.searchPlaceholder}
              </div>
            </div>

            <h1 className=" text-xl sm:text-2xl md:text-3xl mb-2 sm:mb-3 font-medium text-shadow-[2px_2px_8px_#000]/75">
              {content.heading}
            </h1>


            <p className="text-base sm:text-lg md:text-lg font-light leading-relaxed">
              {content.description.top}
            </p>
          </div>

          {/* Description Text */}
          <div className="max-w-[250px] lg:max-w-2xl text-center justify-self-start">
            <p className="font-light text-xs sm:text-xl md:text-2xl">
              {content.description.bottom}
            </p>
          </div>
        </div>

        <Image
          src={commonAssets.curveWhite.src}
          alt={commonAssets.curveWhite.alt}
          width={1280}
          height={150}
          sizes="100vw"
          className="absolute inset-x-0 w-full bottom-0 z-3 pointer-events-none"
        />

        <div className="bg-black/60 absolute inset-0 rounded-t-full z-2 pointer-events-none"></div>
      </div>
    </div>
  );
}

export default SemiCircle;
