"use client";
import React, { useEffect, useRef, useState } from "react";
import RecipeCard from "./RecipeCard";
import { useGlobalStore } from "@/lib/store";
import Link from "next/link";
import { mediaLoader } from "@/lib/media-loader";

const recipes = {
  teal: [
    { image: "/products-page/parantha.png", title: "Parantha" },
    { image: "/products-page/snacks.png", title: "Snacks" },
    { image: "/products-page/sabzi.png", title: "Sabzi" },
    { image: "/products-page/dosa.png", title: "Dosa" },
  ],
  orange: [
    { image: "/products-page/weight-watchers/poha.png", title: "Poha" },
    {
      image: "/products-page/weight-watchers/paneer-bhurji.png",
      title: "Paneer Bhurji",
    },
    {
      image: "/products-page/weight-watchers/dal-tadka.png",
      title: "Dal Tadka",
    },
    {
      image: "/products-page/weight-watchers/stir-fried-veggies.png",
      title: "Stir-fried Veggies",
    },
  ],
};

const RecipeCardContainer = () => {
  const [isVisible, setIsVisible] = useState(false);
  const containerRef = useRef<HTMLDivElement>(null);
  const { theme } = useGlobalStore();
  const isTeal = theme === "teal";

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setIsVisible(true);
          }
        });
      },
      {
        threshold: 0.2,
        rootMargin: "0px 0px -50px 0px",
      }
    );

    if (containerRef.current) {
      observer.observe(containerRef.current);
    }

    return () => observer.disconnect();
  }, []);

  return (
    <>
      <div
        className="px-6 pr-0 sm:px-16 py-8 bg-cover bg-center bg-no-repeat relative"
        style={{
          backgroundImage: `url(${mediaLoader(
            "/products-page/pro-digest-bg.jpg"
          )})`,
        }}
      >
        <div
          ref={containerRef}
          className="flex sm:justify-center sm:gap-5 relative z-11 overflow-x-auto sm:overflow-visible"
        >
          {recipes[theme].map((recipe, index) => (
            <Link
              href={`blogs/${index + 1}`}
              key={`${theme}-${index}`}
              className={`group block py-2 mr-5 sm:m-0 sm:py-3 px-2 sm:px-4 relative min-w-[140px] sm:w-full sm:max-w-[200px] outline-1 outline-white bg-white/70 backdrop-blur-xl rounded-xl transition-all duration-700 ease-out
              ${
                isVisible
                  ? "opacity-100 translate-x-0 sm:translate-y-0"
                  : "opacity-0 translate-x-20 sm:translate-y-10"
              }
            `}
              style={{ transitionDelay: `${index * 100}ms` }}
            >
              <RecipeCard
                image={recipe.image}
                title={recipe.title}
                background={theme as "teal" | "orange"}
              />
            </Link>
          ))}
        </div>

        <div className="bg-black/30  backdrop-blur-sm absolute inset-0 z-10"></div>
      </div>

      <div className="pt-8 px-6 sm:hidden w-full mx-auto">
        <Link
          href="/blogs"
          className="block bg-primary py-2.5 px-4 w-full rounded-md text-white text-lg text-center"
        >
          {isTeal ? "Try a Gut Pro Recipe" : "Try a Weight Watchers Recipe"}
        </Link>
      </div>
    </>
  );
};

export default RecipeCardContainer;
